<template lang="pug">
.nav-top(row v-if="!$root.mobile")
  .tabs(row center f1 v-if="$root.userflow && $root.userflow == 'photographer'")
    .tab(row center :class="{active: $route.path.split('photographer/').last() === tab}" @click="$router.push({ path: '/photographer/' + tab, query: $route.query})" v-for="tab, index in tabs.photographer") {{ t.tab || tab }}
  .tabs(row center f1 v-if="$root.userflow && $root.userflow == 'admin'")
    .tab(row center :class="{active: $route.path.split('admin/').last() === tab}" @click="$router.push({ path: '/admin/' + tab, query: $route.query})" v-for="tab, index in tabs.admin") {{ t.tab || tab }}
  div(row center @click="$root.lang = $root.lang === 'fr' ? 'en' : 'fr'" class="lang-select" v-if="$root.lang")
    svg-icon(name="flag-icon-fr" v-if="$root.lang === 'en'")
    svg-icon(name="flag-icon-en" v-if="$root.lang === 'fr'")
.nav-top(row v-else)
  div menu
  div {{ $route.path.split('photographer/').last().split('/').first() }}
</template>

<script>
export default {
  data() {
    return {
      userflow: null,
      tabs: {
        photographer: ['agenda', 'profile', 'archive'],
        admin: ['photographers']
      },
    }
  },
  methods: {
    log:console.log,
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.nav-top { box-shadow: 0 0 8px 0 rgba(74, 105, 160, .25); padding:0 5px;}
.path .back { display: none; }
.nav-top { z-index: 1010;position: fixed;width: 100%;height: var(--nav-height);text-transform: uppercase;font-size: 80%;line-height: 1.4;background: white;transition: var(--transition); }
.nav-top svg { fill: var(--secondary);width: 20px;height: 20px; }
.nav-top .logo, .nav-top .title { z-index: 1; }
.nav-top .logo { padding: 8px;padding-left: 24px; }
.nav-top .logo img { width: inherit;height: inherit; }
.nav-top .logo svg { position: absolute;left: 5px; }
.nav-top .title svg { margin-top: -2px;margin-left: 8px;width: 11px;height: 11px;border: var(--border);border-radius: var(border-radius); }
.nav-top .title .screen { font-weight: 700; }
.nav-top .title .period { font-weight: 500;color: var(--secondary); }
.nav-top .version { margin-top: auto; }
.tabs .tab { height: var(--nav-height);padding: 0 24px;color: var(--inactive); border-bottom: 2px solid var(--border-light);border-radius: 1px}
.tabs .tab:hover { cursor: pointer;background: var(--background); }
.tabs .tab.active { color: var(--primary);border-bottom: 2px solid var(--primary);border-radius: 1px; }
.nav-top .reset { position: absolute;top: 0;right: 88px; }
.nav-top :matches(.title, .reset, .filters, .menu) { padding: 0 12px;height: 60px; }
.nav-top :matches(.title, .reset, .filters, .menu):hover { cursor: pointer;background: var(--background-2); }
.mobile:not(.tablet) .nav-top .tabs { display: none; }
.mobile:not(.tablet) .nav-top .title { flex: 1; }
.lang-select { cursor: pointer;}

</style>



