<template lang="pug">
.modal-transfer
  .title {{ t.transfer_title }}
  fieldset
    label(for="photographer") {{ t.type_code_email_name }}
    input(id="photographer" :class="{'no-border-bottom': search_result && search_result.length}" type="text" placeholder="CODE, email@gmail.com, firstname" v-model="photographer" @focus="selected = null")
    .results(v-if="search_result && search_result.length && !selected")
      .result(v-for="result in search_result" @click="selected = result") {{result.photographer_id}} - {{ result.ph_firstname }}
  .selected(v-if="selected") {{ t.transfer_to }} {{ selected.photographer_id }}
  .buttons(row right)
    button.flat(@click="$root.modal.name = null") {{ t.cancel }}
    button.action.primary(@click="log('transfer')") {{ t.transfer }}
</template>
<script>
export default {
  data() {
    return {
      photographer: null,
      test: null,
      photographers: [{'ph_firstname': 'Vincent','photographer_id':'VIDE'}, {'ph_firstname': 'Nazim','photographer_id':'NABE'}],
      search_result: null,
      selected: null,
    }
  },
  watch: {
    photographer: debounce(function (search) {
      if (!search) return this.search_result = null
      return this.search_result = this.photographers.filter(d => d.some(v => new RegExp(search, 'i').test(v)))
    }, 300)
  }

}
</script>
<style scoped>
.selected {
  padding: 10px;
}
</style>



