<template lang="pug">
.vue-modal
  transition(name="fade")
    .overlay(@click="name = null" v-if="name")
      .modal(row center)
        .container(block v-on:click.prevent.stop="")
          svg.close(@click="name = null")
            line(fill="none" stroke="#000" stroke-width="2" stroke-miterlimit="10" x1="2" y1="2" x2="18" y2="18")
            line(fill="none" stroke="#000" stroke-width="2" stroke-miterlimit="10" x1="18" y1="2" x2="2" y2="18")
          .content
            modal-transfer(v-if="name === 'transfer'")
            modal-editph(:data="data" v-if="name === 'editph'")
</template>
<script>
export default {
  data() {
    return { photographer: null, test: null, name: null, data: null}
  },
  mounted() {
    this.$emit('mounted', this)
  },
  watch: {
    photographer: debounce(function (newVal) {
      this.test = newVal
    }, 300)
  }

}
</script>
<style>
.vue-modal .overlay {
  z-index: 10000;
  position: fixed;
  top: 0;
  left: 0;
  min-width: 100vw;
  min-height: 100vh;
  background: rgba(0, 0, 0, .5);
  cursor: pointer;
}
.vue-modal .fade-enter-active, .fade-leave-active {
  opacity: 1;
  transition: var(--transition);
}
.vue-modal .fade-enter, .fade-leave-active {
  opacity: 0;
}
.vue-modal .content .title {
  font-size: 120%;
  text-align: center;
  margin-bottom: 30px;
}
.vue-modal .modal {
  margin-top:100px;
}
.vue-modal .container {
  position:relative;
  padding-top: 40px;
}
.vue-modal .close {
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 40px;
  padding: 10px;
}
.vue-modal .content .results {
  padding: 10px 0;
  width: 300px;
  border: 2px solid var(--primary);
  border-top: 1px solid var(--primary);
}
.vue-modal .content .result {
  padding: 0 10px;
}
.vue-modal .content .result:hover {
  background: var(--highlight);
}
.vue-modal .content .buttons {
  margin-top: 20px;
}
.no-border-bottom {
  border-bottom: none!important;
}
</style>



