window.log = Vue.prototype.log = console.log.bind(console)
window.update_query = Vue.prototype.update_query = query => vm.$router.push({ query: vm.$route.query.add(query).filter((v, k) => v) })
window.update_filter = Vue.prototype.update_filter = (k, v) => {
  const query = Sugar.Object.clone(vm.$route.query)
  query[k] = query[k] ? query[k].split('|') : []
  v.split('|').map(v => query[k] = query[k].includes(v) ? query[k].remove(v) : query[k].add(v))
  query[k] = query[k].join('|')

  return update_query(query)
}
window.size = Vue.prototype.size = () => {
  let size = ''
  if (window.innerWidth > 1500) size += 'desktop4K '
  if (window.innerWidth > 1200) size += 'desktop '
  if (window.innerWidth > 900) size += 'laptop '
  if (window.innerWidth > 600) size += 'tablet '
  return size += 'mobile'
}


window.minutes_to_time = Vue.prototype.minutes_to_time = (minutes) => {
 var h = Math.floor(minutes / 60);
 var m = minutes % 60;
 if (h === 0) return minutes + 'm'
 h = h < 10 ? '0' + h : h;
 m = m < 10 ? '0' + m : m;
 return h + 'h' + m + 'm';
}
window.debounce = Vue.prototype.debounce = (fn, delay) => {
  var timeoutID = null
  return function () {
    clearTimeout(timeoutID)
    var args = arguments
    var that = this
    timeoutID = setTimeout(function () {
      fn.apply(that, args)
    }, delay)
  }
}



