import navTop from './navTop.vue'
import navLeft from './navLeft.vue'
import vueCollapse from './vueCollapse.vue'
import vueModal from './vueModal.vue'
import sessionCard from './sessionCard.vue'
import svgIcon from './svgIcon.vue'
import svgSprite from './svgSprite.vue'
import modalTransfer from './modalTransfer.vue'
import modalEditph from './modalEditph.vue'




const components = {
  navTop,
  svgIcon,
  svgSprite,
  vueCollapse,
  sessionCard,
  vueModal,
  modalTransfer,
  modalEditph,
  navLeft,
}

export default {
  install(Vue) {
    Object.keys(components).map(name => Vue.component(name, components[name]))
    // Object.keys(directives).map(name => Vue.directive(name, directives[name]))
  },
}



