<template lang="pug">
.session-card(v-if="!$root.mobile && session")
  .card(block @click="update_query({session: session.session_id})")
    div(grid)
      div(column style="flex: 2 0 auto")
        .type {{ t[session_type] }} - {{ session.session_id}}
        div(grid)
          .info(row)
            svg-icon(name="pt-icon-calendar")
            .text {{ day }}
          .info(row)
            svg-icon(name="pt-icon-clock")
            .text {{ time }}
        div(grid)
          .info(row)
            svg-icon(name="pt-icon-stopwatch")
            .text {{ minutes_to_time(session.duration) }}
          .info(row)
            svg-icon(name="pt-icon-map-marker")
            .text {{ session.location_id}}
      div(column center style="flex: 1 0 auto" v-if="['exclusive','agenda'].includes(session_type)")
        button.action.primary {{ t.accept }}
        button.action.secondary {{ t.decline }}
      div(column center style="flex: 1 0 auto" v-if="['upcoming'].includes(session_type)")
        button.action {{ t.reschedule }}
        button.action.secondary {{ t.cancel }}
.session-card(v-else-if="$root.mobile && session")
  vue-collapse(:opened="$route.query.session === session.session_id")
    template(slot="header")
      .parent
          .card(grid :class="{active: $route.query.session === session.session_id }" @click="update_query({session: ($route.query.session === session.session_id) ? null : session.session_id})")
            div(column style="flex: 2 0 auto")
              .type {{ t[session_type] }} - {{ session.session_id}}
              div(grid)
                .info(row)
                  svg-icon(name="pt-icon-calendar")
                  .text {{ day }}
                .info(row)
                  svg-icon(name="pt-icon-clock")
                  .text {{ time }}
              div(grid)
                .info(row)
                  svg-icon(name="pt-icon-stopwatch")
                  .text {{ minutes_to_time(session.duration) }}
                .info(row)
                  svg-icon(name="pt-icon-map-marker")
                  .text {{ session.location_id}}
    template(slot="content")
      .mobile-content
        .buttons(row center style="flex: 1 0 auto" v-if="['exclusive','agenda'].includes(session_type)")
          button.action.primary(v-on:swipeleft="log('azea')") {{ t.accept }}
          button.action.secondary {{ t.decline }}
        .buttons(row center style="flex: 1 0 auto" v-if="['upcoming'].includes(session_type)")
          button.action {{ t.reschedule }}
          button.action.secondary {{ t.cancel }}
        .buttons(row center v-if="['exclusive','agenda','upcoming'].includes(session_type)")
          button.action(@click="$root.modal.name = 'transfer'") {{ t.transfer }}
          button.action.secondary {{ t.report }}
</template>

<script>
export default {
  props: ['session'],
  data() {
    return {active: false}
  },
  computed: {
    day() {
      return new Date(this.session.date_start).format('%d / %m / %y')
    },
    time() {
      return new Date(this.session.date_start).format('%H:%M')
    },
    session_type() {
      if (this.session.status_code === 1020) return 'exclusive'
      if (this.session.status_code === 1025) return 'agenda'
      if (this.session.status_code >= 1030 && new Date(this.session.date_start) > new Date()) return 'upcoming'
    }
  },
  methods: {
    pan(ev) {
      const path = Array.prototype.slice.call(ev.srcEvent.path)
      const el = path.find(d => (typeof(d.className) === 'string' && d.className.includes('swipe')))
      el.style.transform = `translateX(${ev.deltaX}px)`
      el.parentElement.style.background = (ev.deltaX < 0) ? 'var(--secondary)' : 'var(--primary)'

    },
    panend(ev) {
      const path = Array.prototype.slice.call(ev.srcEvent.path)
      const el = path.find(d => (typeof(d.className) === 'string' && d.className.includes('swipe')))
      el.style.transform = `translateX(0px)`
      if (ev.pointers[0].clientX < 15 || Math.abs(window.innerWidth - ev.pointers[0].clientX) < 15) alert('aezaez')
      console.log(ev)
      console.log(Math.abs(window.innerWidth - ev.pointers[0].clientX))
    }
  }
}

</script>
<style scoped>
.session-card .card {
  background: #fff;
  /*height: 120px;*/
}
.card[block] {margin: var(--spacing) 0;}
.tablet .session-card .card {
  min-width: 450px;
}
.mobile:not(.tablet) .session-card .card {
  width:100%;
  padding: 10px;
  border-bottom: 1px solid rgba(0,0,0,0.1);
}
.swipe {background: #fff;}
.mobile:not(.tablet) .session-card .card:active {
 background: rgba(0,0,0,0.1);
}
.mobile:not(.tablet) .session-card .card.active {
  box-shadow: 0 2px 18px 0 rgba(74, 105, 160, 0.1);
  background: rgba(46, 200, 193,0.2);
}
.mobile-content {
  padding: 10px;
}
.type {
  font-weight: 600;
}
.info {
  padding: 4px;
}
.info .svg-icon {
  margin: auto 0;
  width:18px;
  height:18px;
}
.info .text {
  padding-left: 15px;
}

.tablet .session-card:hover {
  box-shadow: 0 3px 10.5px 0 rgba(74, 105, 160, 0.30);
}
.tablet .session-card button {  min-width: 90%;
}
.mobile:not(.tablet) button {
  flex: 1 0 auto;
}
.mobile-content .buttons { margin: 5px;}
</style>



