<template lang="pug">
.modal-editph
  .title {{ t.additionnal_fields }}
  fieldset(v-for="col in data")
    label {{ col.name }}
    textarea(:value="col.original_value" v-if="col.type === 'text'")
    input(:value="col.original_value" v-else)
  .buttons(row right)
    button.flat(@click="$root.modal.name = null") {{ t.cancel }}
    button.action.primary(@click="wip") {{ t.validate }}
</template>
<script>
export default {
  props: ['data'],
  methods: {
    wip() {
      alert('Ne marche pas encore')
    },
  }
}
</script>
<style scoped>
.modal-editph {
  overflow: auto;
}
</style>



