<template lang="pug">
//- .vue-collapse
//-   .collapse-header(row @click="collapse")
//-     svg-icon(:name="active ? 'pt-icon-minus' : 'pt-icon-plus'")
//-     .title {{ t[title] || title.titleize() }}
//-   .content
//-     slot
.vue-collapse
  .collapse-header
    slot(name="header")
  .content
    slot(name="content")
</template>
<script>
export default {
  props: ['title', 'opened'],
  data() {
    return {active: false}
  },
  watch: {
    opened() {
      if (this.opened) return this.open()
      return this.close()
    }
  },
  methods: {
    open() {
      var content = this.$el.querySelector('.content')
      content.style.maxHeight = content.scrollHeight + "px"
      this.active = true
    },
    close() {
      var content = this.$el.querySelector('.content')
      content.style.maxHeight = null
      this.active = false
    },
    collapse(el) {
      var content = this.$el.querySelector('.content')
      if (content.style.maxHeight){
        content.style.maxHeight = null
        this.active = false
      } else {
        content.style.maxHeight = content.scrollHeight + "px"
        this.active = true
      }
    }
  }
}

</script>
<style scoped>
.collapse-header {
  cursor: pointer;
}
.content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}

</style>



