<template lang="pug">
section.page-ph-session(grid-box v-if="!$root.mobile")
  div(grid)
    .section(style="flex: 1.2 0")
      vue-collapse(:title="k" v-for="v,k in filters")
        .filters
          .filter(row @click="update_query({[k]: !$route.query[k] || $route.query[k] !== 'all' ? 'all' : null })")
            .square(:class="{active: $route.query[k] === 'all' }")
            .name {{ t.all }}
          .filter(row @click="update_filter(k,name)" v-for="name in v")
            .square(:class="{active: ($route.query[k] && $route.query[k].split('|').includes(name)) || $route.query[k] === 'all'}")
            .name {{ t[name] || name }}
    .section(style="flex: 2 0")
      session-card.clickable(:session="session" :class="{active: $route.query.session === session_id }" v-for="session, session_id in sessions")
    .section.details(block column style="flex: 2 0 400px;")
      .content(v-if="!active_session || ['agenda', 'exclusive'].includes(active_session_type)")
        .title Instructions
        div blablabla
      .content(column v-else-if="['upcoming'].includes(active_session_type)")
        .title {{ active_session.client_firstname }}
        .main-infos
          .info(row)
            svg-icon(name="pt-icon-mail")
            .text.spaced {{ active_session.client_email }}
          .info(row)
            svg-icon(name="pt-icon-phone")
            .text.spaced {{ active_session.client_phone }}
          .info(row)
            svg-icon(name="pt-icon-camera")
            .text.spaced {{ active_session.session_type }}
        div(row)
          .img-container
            img(src="../assets/img/FR-Marseille.jpg")
          .extra-infos(column)
            .info(row)
              svg-icon(name="pt-icon-calendar")
              .text {{ active_session_day }}
            .info(row)
              svg-icon(name="pt-icon-clock")
              .text {{ active_session_time }}
            .info(row)
              svg-icon(name="pt-icon-stopwatch")
              .text {{ minutes_to_time(active_session.duration) }}
            .info(row)
              svg-icon(name="pt-icon-map-marker")
              .text {{ active_session.location_id}}
        .title {{ t.client_comment }}
        .comment {{ active_session.comment }}
      .content(v-else)
      .actions
        h3 {{ t.other_actions }}
        div(row right v-if="['exclusive','agenda','upcoming'].includes(active_session_type)")
          button.action(@click="$root.modal.name = 'transfer'") {{ t.transfer }}
          button.action.secondary {{ t.report }}
section.page-ph-session(column v-else)
  session-card.clickable(:session="session" :class="{active: $route.query.session === session_id }" v-for="session, session_id in sessions" :opened="$route.query.session === session_id")
  session-card.clickable(:session="session" :class="{active: $route.query.session === session_id }" v-for="session, session_id in sessions" :opened="$route.query.session === session_id")
  .filler
</template>
<script>
export default {
  data() {
    return {
      filters: {
        invitations: ['exclusive', 'agenda'],
        sessions: ['upcoming', 'pending_validation', 'pending_publication']
      },
      sessions: Object.assign(['REFSESSION1','REFSESSION2'].reduce((acc,v) => {
        acc[v] = {
          date_start: '2018-06-20 10:30:00',
          duration: 30,
          location_id: 'FR-Marseille',
          time_left: 127,
          status_code: 1020,
          session_id: v,
          client_firstname: 'Anabel',
          client_email: 'anabel@everphotoshoot.com',
          client_phone: '+33612121212',
          comment: 'Je ne me trouve pas photogénique',
          session_type: 'SOLO - Book'
        }
        return acc
      }, {}),['SESSIONACCEPT','SESSIONACCEPT2'].reduce((acc,v) => {
        acc[v] = {
          date_start: '2018-06-20 10:30:00',
          duration: 30,
          location_id: 'FR-Marseille',
          time_left: 127,
          status_code: 1030,
          session_id: v,
          client_firstname: 'Anabel',
          client_email: 'anabel@everphotoshoot.com',
          client_phone: '+33612121212',
          comment: 'Je ne me trouve pas photogénique',
          session_type: 'SOLO - Book'
        }
        return acc
      }, {}))
    }
  },
  computed: {
    active_session() {
      return this.$route.query.session && this.sessions[this.$route.query.session]
    },
    active_session_type() {
      if (!this.active_session) return
      if (this.active_session.status_code === 1020) return 'exclusive'
      if (this.active_session.status_code === 1025) return 'agenda'
      if (this.active_session.status_code >= 1030 && new Date(this.active_session.date_start) > new Date()) return 'upcoming'
    },
    active_session_day() {
      if (!this.active_session) return
      return new Date(this.active_session.date_start).format('%d / %m / %y')
    },
    active_session_time() {
      if (!this.active_session) return
      return new Date(this.active_session.date_start).format('%H:%M')
    },
  }
}
</script>
<style scoped>
.section {
  padding: 20px;
}
::-webkit-scrollbar {
    width: 0px;  /* remove scrollbar space */
    background: transparent;  /* optional: just make scrollbar invisible */
}
.filters {
  padding-left: 29px;
}
.filters .square {
  width:18px;
  height:18px;
  background: white;
  border-radius: 4px;
  border: 1px solid var(--border-light);
  margin: auto 0;
}
.filters .square.active {
  background: black;
}
.filters .name {
  padding-left: 20px;
}
.filter {
  padding: 5px;
}
.filter:hover {
  background: rgba(0,0,0,.100);
  cursor: pointer;
}
.tablet .session-card.active {
  outline: 2px solid var(--primary);
}
.mobile:not(.tablet) .session-card.active {
  /*background: rgba(0,0,0,.100);*/
}
.section.details {
  margin-top: calc(var(--spacing) + 20px);
  background: #fff;
}
.details .content {
  flex: 1 0;
}
.info .text.spaced {
  padding-left: 30px;
}
.details .main-infos {
  margin-bottom: 20px;
}
.details .extra-infos {
  padding: 20px;
}
.details .img-container {
  width:300px;
  margin-bottom: 20px;
}
.filler { flex: 1 0; background: #fff; }
</style>



