import Vue from 'vue'
import Router from 'vue-router'
import PagePhSession from '../views/PagePhSession.vue'
import PagePhProfile from '../views/PagePhProfile.vue'
import PageAdminPh from '../views/PageAdminPh.vue'
import PageTest from '../views/PageTest.vue'

Vue.use(Router)
export default new Router({
  mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    { path: '/test', component: Vue.component('PageTest', PageTest) },
    { path: '/admin/photographers', component: Vue.component('PageAdminPh', PageAdminPh), meta: { userflow: 'admin' } },
    { path: '/photographer/agenda', component: Vue.component('PagePhSession', PagePhSession), meta: { userflow: 'photographer' } },
    { path: '/photographer/archive', component: Vue.component('PagePhSession', PagePhSession), meta: { userflow: 'photographer' } },
    { path: '/photographer/profile', component: Vue.component('PagePhProfile', PagePhProfile), meta: { userflow: 'photographer', navLeft: true } },
    // { path: '/login', component: Vue.component('PageLogin', PageLogin), meta: { access: ['anonymous'] } },
    { path: '*', redirect: '/admin/photographers' },
  ],
  linkActiveClass: 'active',
  linkExactActiveClass: 'exact',
})



