<style src="./assets/css/raw.css" />
<style src="./assets/css/layout.css" />
<template>
  <main id="app" :class="size">
    <vue-modal @mounted="modal = $event" />
    <svg-sprite />
    <nav-top />
    <nav-left v-if="$route.meta && $route.meta.navLeft"/>
    <router-view class="page" :key="$route.path" ref="router" />
  </main>
</template>

<script>

export default {
  name: 'app',
  data() { return {lang: localStorage.lang || 'fr', modal: null, size: size() } },
  computed: {
    userflow() {
      return this.$route.meta && this.$route.meta.userflow
    },
    mobile() {
      return this.size.includes('tablet') ? false : true
    }
  },
  watch: {
    lang() { localStorage.lang = this.lang || 'fr' },
  },
  methods: {
    log:console.log,
  },
  mounted() {
    window.addEventListener('resize', () => this.size = size())
  },

}
</script>

<style>
main {
  height:100vh;
  position:relative;
}
</style>



